@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
:root {
  --FF: "Inter", sans-serif;
}

:root {
  --cardHoverDark: hsl(227, 7%, 74%);
  --hover: var(--cardHoverDark);
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  /* --Instagram: linear-gradient(to right, hsl(37, 97%, 70%), hsl(329, 70%, 58%)); */
  --YouTube: hsl(348, 97%, 39%);
  --Instagram: orange;
  --ToogleLightBtn: hsl(0, 0%, 100%);
  --ToggleBGColor: hsl(230, 22%, 74%);
  --ToggleBG: var(--ToggleBGColor);
  --WhiteBG: hsl(0, 0%, 100%);
  --VeryPaleBlueTopBGPattern: hsl(225, 100%, 98%);
  --LightGrayishBlueCardBG: hsl(227, 47%, 96%);
  --VeryDarkBlueText: hsl(230, 17%, 14%);
  --cardBG: var(--LightGrayishBlueCardBG);
  --DarkGrayishBlueText: hsl(228, 12%, 44%);
  --lightBackground:linear-gradient(var(-VeryPaleBlueTopBGPattern), var(---WhiteBG));
  --ToggleBtn: var(--ToogleLightBtn);
  --background: var(--lightBackground);
  --cardTextColor: var(--DarkGrayishBlueText);
  --cardFC: var(--VeryDarkBlueText);
  --TextColor: var(--VeryDarkBlueText);
  --cardBG: var(--LightGrayishBlueCardBG);
}

[data-theme=dark] {
  --VeryDarkBlueBG: hsl(230, 17%, 14%);
  --VeryDarkBlueTopBGPattern: hsl(232, 19%, 15%);
  --DarkDesaturatedBlueCardBG: hsl(228, 28%, 20%);
  --DesaturatedBlueText: hsl(228, 34%, 66%);
  --WhiteText: hsl(0, 0%, 100%);
  --darkBackground: linear-gradient(var(--VeryDarkBlueTopBGPattern), var(--VeryDarkBlueBG));
  --ToggleBG: linear-gradient(225deg, hsl(210, 78%, 56%), hsl(146, 68%, 55%));
  --ToggleDarkBtn: hsl(230, 17%, 14%);
  --cardHoverDark: hsl(228, 25%, 27%);
  --hover: var(--cardHoverDark);
  --ToggleBtn: var(--ToggleDarkBtn);
  --background: var(--darkBackground);
  --cardTextColor: var(--DarkGrayishBlueText);
  --cardFC: var(--WhiteText);
  --TextColor: var(--DesaturatedBlueText);
  --cardBG: var(--DarkDesaturatedBlueCardBG);
}

@media (prefers-color-scheme: dark) {
  :root {
    --limeGreen: hsl(163, 72%, 41%);
    --brightRed: hsl(356, 69%, 56%);
    --Facebook: hsl(208, 92%, 53%);
    --Twitter: hsl(203, 89%, 53%);
    /* --Instagram: linear-gradient(to right, hsl(37, 97%, 70%), hsl(329, 70%, 58%)); */
    --YouTube: hsl(348, 97%, 39%);
    --Instagram: orange;
    --VeryDarkBlueBG: hsl(230, 17%, 14%);
    --VeryDarkBlueTopBGPattern: hsl(232, 19%, 15%);
    --DarkDesaturatedBlueCardBG: hsl(228, 28%, 20%);
    --DesaturatedBlueText: hsl(228, 34%, 66%);
    --WhiteText: hsl(0, 0%, 100%);
    --darkBackground: linear-gradient(var(--VeryDarkBlueTopBGPattern), var(--VeryDarkBlueBG));
    --ToggleBG: linear-gradient(225deg, hsl(210, 78%, 56%), hsl(146, 68%, 55%));
    --ToggleDarkBtn: hsl(230, 17%, 14%);
    --cardHoverDark: hsl(228, 25%, 27%);
    --hover: var(--cardHoverDark);
    --ToggleBtn: var(--ToggleDarkBtn);
    --background: var(--darkBackground);
    --cardTextColor: var(--DarkGrayishBlueText);
    --cardFC: var(--WhiteText);
    --TextColor: var(--DesaturatedBlueText);
    --cardBG: var(--DarkDesaturatedBlueCardBG);
  }
  [data-theme=light] {
    --ToogleLightBtn: hsl(0, 0%, 100%);
    --ToggleBGColor: hsl(230, 22%, 74%);
    --ToggleBG: var(--ToggleBGColor);
    --cardHoverDark: hsl(227, 7%, 74%);
    --hover: var(--cardHoverDark);
    --WhiteBG: hsl(0, 0%, 100%);
    --VeryPaleBlueTopBGPattern: hsl(225, 100%, 98%);
    --LightGrayishBlueCardBG: hsl(227, 47%, 96%);
    --VeryDarkBlueText: hsl(230, 17%, 14%);
    --cardBG: var(--LightGrayishBlueCardBG);
    --DarkGrayishBlueText: hsl(228, 12%, 44%);
    --lightBackground:linear-gradient(var(-VeryPaleBlueTopBGPattern), var(---WhiteBG));
    --ToggleBtn: var(--ToogleLightBtn);
    --background: var(--lightBackground);
    --cardTextColor: var(--DarkGrayishBlueText);
    --cardFC: var(--VeryDarkBlueText);
    --TextColor: var(--VeryDarkBlueText);
    --cardBG: var(--LightGrayishBlueCardBG);
  }
}
*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: var(--FF);
  font-size: 100%;
  background: var(--background);
  color: var(--WhiteText);
  min-height: 150vh;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  color: var(--cardTextColor);
  background-color: var(--cardBG);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  cursor: pointer;
  transition: background 150ms ease-in-out;
}
.card:hover {
  background-color: var(--hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--Facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--Twitter);
}
.card--instagram {
  border-top: 0.3125rem solid var(--Instagram);
}
.card--youtube {
  border-top: 0.3125rem solid var(--YouTube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card__followers {
  margin-bottom: 1.5rem;
}
.card__count {
  color: var(--cardFC);
  font-weight: bold;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  color: var(--DesaturatedBlueText);
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: bold;
}
.card__change--up {
  color: var(--limeGreen);
}
.card__change--down {
  color: var(--brightRed);
}
.card__change img {
  margin-right: 0.25rem;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid black;
}
.toggle__background {
  display: block;
  position: absolute;
  border-radius: 0.75rem;
  height: 100%;
  width: 100%;
  top: 0;
  background: var(--ToggleBG);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  border-radius: 50%;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  background-color: var(--ToggleBtn);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 1.3125rem);
  right: 0.1875rem;
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
  color: var(--TextColor);
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--TextColor);
}
.header__toggle {
  margin-left: auto;
}

#toggle-display {
  color: var(--TextColor);
  font-weight: 400;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}